/* ================= GLOBAL ================= */
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: #f5f5f7;
  margin: 0;
  padding: 0;
  color: #222;
}

a {
  text-decoration: none;
  color: #0077cc;
}

h1, h2, h3 {
  margin: 0.5em 0;
}

/* ================= NAV ================= */
nav {
  display: flex;
  justify-content: space-around;
  background: #0077cc;
  color: #fff;
  padding: 0.5em;
  position: sticky;
  top: 0;
  z-index: 100;
}

nav a {
  color: white;
  font-weight: bold;
}

/* ================= CARDS ================= */
.card {
  background: #fff;
  padding: 1em;
  margin: 1em;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

/* ================= HOME ================= */
img {
  max-width: 120px;
  margin-bottom: 0.5em;
}

ul {
  padding-left: 1.2em;
}

/* ================= SERVICES ================= */
.card button, .card a {
  margin: 0.3em 0.2em;
  padding: 0.5em 1em;
  border: none;
  border-radius: 6px;
  background: #0077cc;
  color: #fff;
  cursor: pointer;
  font-weight: bold;
  transition: 0.2s;
}

.card button:hover, .card a:hover {
  background: #005fa3;
}

/* ================= POS ================= */
.amount-display {
  font-size: 2em;
  text-align: center;
  margin: 1em 0;
  color: #0077cc;
  font-weight: bold;
}

.keypad {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5em;
  margin-bottom: 1em;
}

.keypad button {
  padding: 1em;
  font-size: 1.2em;
  border-radius: 8px;
}

.payment-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.6);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.payment-overlay button {
  margin-top: 1em;
  background: #ffcc00;
  color: #222;
}

/* ================= ADMIN ================= */
.card input, .card textarea, .card select {
  width: 100%;
  padding: 0.5em;
  margin: 0.3em 0;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 1em;
}

/* ================= RESPONSIVE ================= */
@media(max-width:600px){
  nav {
    flex-direction: column;
    align-items: center;
  }

  .keypad {
    grid-template-columns: repeat(3, 1fr);
    gap: 0.3em;
  }

  .card {
    margin: 0.5em;
  }

  .amount-display {
    font-size: 1.5em;
  }
}

